:root {
  --active-color: #00acdc;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo-pic {
  height: 40px;
  margin: 10px 0 6px;
}

.list {
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.content, .is-active {
  white-space: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition-duration: .3s;
  display: flex;
  position: relative;
}

.list__item {
  margin: 0 10px;
}

.list__item:last-child {
  margin-right: 0;
}

.list__item:first-child {
  margin-left: 0;
}

.content:hover, .is-active {
  color: var(--active-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

@media (width <= 767px) {
  .list__item:nth-child(5), .list__item:nth-child(6), .list__item:nth-child(7), .list__item:nth-child(8) {
    display: none;
  }
}

@media (width <= 500px) {
  .list__item:nth-child(3), .list__item:nth-child(2) {
    display: none;
  }
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
  display: block;
}

.card__pic {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 140%;
}

.card__reviews {
  color: #060b35;
  text-align: center;
  margin-top: 3px;
  font-size: 10px;
  font-weight: 400;
}

.card__reviews-wrap {
  height: 16px;
}

.card__reviews-wrap, .card__price-wrap {
  justify-content: space-between;
  display: flex;
}

.card__price {
  color: #616070;
  margin-top: 24px;
  margin-bottom: 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 150%;
  display: inline-block;
}

.card__price_sum {
  color: #060b35;
  text-align: right;
  margin-top: 24px;
  margin-bottom: 16px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  display: inline-block;
}

.card__buy {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  display: block;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.catalog {
  justify-content: center;
  padding: 50px 40px 72px;
  display: flex;
}

.catalog__cover {
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  display: grid;
}

@media (width <= 488px) {
  .catalog__cover {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .catalog__cover {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog__cover {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog__cover {
    grid-template-columns: repeat(4, 200px);
  }
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

